
import React, { useEffect, useState } from 'react';


import { useHistory, useParams } from 'react-router-dom';

//yarn add styled-components
import styled from 'styled-components';

//yarn add node-sass
//import './Detail.css';
import './Detail.scss';


let 박스 = styled.div`
  padding : 20px
`;

let 제목 = styled.h4`
  font-size : 25px;
  color : ${ props => props.색상 }
`;


function Detail(props){ 

    let [dds, dds변경] = useState('my-alert2');
    let [inputDa, inputDa변경] = useState();
    useEffect(()=> {
      //console.log(111111);
      //2초후 alert삭제 
      let 타이면 = setTimeout(()=>{  dds변경("my-alert3");  }, 2000)

      //return function 어쩌구 (){ clearTimeout(타이면);}
      return ()=> { clearTimeout(타이면);}
    }, [] );

    let history = useHistory();
    let { id } = useParams();
    return (
        <div className="container">
          <박스>
            <제목 className='red'>상세페이지</제목>
          </박스>
          {inputDa}
          <input type="text" onChange={(e)=>{
            inputDa변경(e.target.value)
          }}/> 
          <div id='dsf' className={ dds }>
            <p>재고가 얼마 남지 않았습니다</p>
          </div>
          <div className="row">
            <div className="col-md-6"> 
              <img src={"https://codingapple1.github.io/shop/shoes"+eval(eval(id)+1)+".jpg"} width="100%" />
            </div>
            <div className="col-md-6 mt-4">
              <h4 className="pt-5">{props.shoes[id].title}</h4> 
              <p>{props.shoes[id].content}</p>
              <p>{props.shoes[id].price}</p>
              <Info 재고={props.재고}></Info>

              <button className="btn btn-danger" onClick={
                ()=>{
                  props.재고변경([9, 10, 11]);
                }
              }>주문하기</button> 
              <button className="btn btn-danger" onClick={()=>{
                history.goBack();
                //history.push('/');
              }}>뒤로가기</button> 
            </div>
          </div>
        </div>
    )

}
function Info (props) {
  return (
    <p>재고 : {props.재고[0]} </p>
  )
}
export default Detail; 



